@charset "UTF-8";

select + .select2 {
	width: 100% !important;
}

#result_length {
	& > label {
		display: inlnie-block;
		margin-right: 10px;
	}
}

#cancel {
	margin-bottom: 0;
}

@media (max-width: 768px){
	.modal {
		margin-left: 0;

		.modal-dialog {
			top: 30%;
		}
	}
}

@media (min-width: 991px){
	.modal {
		margin-left: 230px;
		
		.modal-dialog {
			top: 30%;
		}
	}
}
